/**
 * @descrption ajax
 */

import { HttpHelper } from '@libs/plugins/http.js';
import sessionUtils from '@data/session/sessionUtils.js';

const TOKEN_HEADER_NAME = 'Authorization';
const LOGIN_PAGE_ROUTE = '/';

class ResponseCode {
    static get LOGIN_EXPIRATION2() { return 20001; }   // 
    static get LOGIN_EXPIRATION() { return 20001; }   // 
    static get LOGIN_EXCEPTION() { return 9999; }     // 
}

const toast = (code, message) => {
    if (window.appContext && window.appContext.$message) {
        let errorLabel = '';
        switch (true) {
            case code === 403: errorLabel = ''; break;
            case code === 404: message = message || ''; break;
            case code >= 500 && code < 600: errorLabel = ''; break;
            default: break;
        }
        window.appContext.$message({ type: 'error', message: `${errorLabel}: ${message}` });
    }
};

export default {
    /**
     * @description config
     *              Promise;
     * @param {Boolean} success 
     * @param {String} url url
     * @param {Object} params 
     * @param {Object} config 
     * @param {Object} router 
     */
    beforeRequest({ success, url, params, config, router }) {
        if (success) {
            // token
            let token = sessionUtils.findToken();
            if(token) config.headers[TOKEN_HEADER_NAME] = token;
            return config;
        }
        
    },
     /**
     * @description Promise
     * @param {Boolean} success axiosPromise resolvetruerejectfalse
     * @param {String} url url
     * @param {Object} params 
     * @param {Object} response 
     * @param {String} responseType 
     * @param {Number} statusCode 
     * @param {Object} router 
     */
    afterResponse({ success, url, params, response, responseType, statusCode, router }) {

        if (success) {
            if (statusCode == 200 && response.data) {
                // console.log('need login', success, statusCode, response.data)
                if (response.data.code == ResponseCode.LOGIN_EXPIRATION || response.data.code == ResponseCode.LOGIN_EXPIRATION2) {                    
                    // 
                    sessionUtils.removeToken();
                    sessionUtils.removeUserInfo();
                    toast(statusCode, '');
                    setTimeout(() => {
                        location.reload(true);
                    }, 1000);
                    // if (router && router.history.current.path !== LOGIN_PAGE_ROUTE) {
                    //     router.replace({ path: LOGIN_PAGE_ROUTE, query: { 
                    //         return: encodeURIComponent(router.history.current.fullPath) }});
                    // }
                    return Promise.reject(response.data.msg);
    
                } else if (response.data.code == ResponseCode.LOGIN_EXCEPTION) {
                    // 
                    sessionUtils.removeToken();
                    sessionUtils.removeUserInfo();
                    toast(statusCode, response.data.msg);
                    if (router && router.history.current.path !== LOGIN_PAGE_ROUTE) {
                        router.replace({ path: LOGIN_PAGE_ROUTE, query: { return: '' }});
                    }
                    return Promise.reject(response.data.msg);
    
                } else if (response.data.code != 0 && response.data.msg) {
                    // toast(response.data.code, response.data.msg);
                }
            }

        } else {
            // 
            if ([404].indexOf(statusCode) == -1) {
                let errMsg = '';
                if (['arraybuffer', 'blob'].indexOf(responseType) > -1) {
                    HttpHelper.parseBlobToString(response.data).then(data => {
                        let dataObj = JSON.parse(data);
                        if (dataObj && dataObj.msg) errMsg = dataObj.msg;
                        toast(statusCode, errMsg);
                    });

                } else {
                    let dataObj = response.data;
                    if (dataObj && dataObj.msg) errMsg = dataObj.msg;
                    toast(statusCode, errMsg);
                }

            } else {
                toast(statusCode, response.data.msg);
            }
        }
    }
};


