import { constantVar } from '../../configs/constantVar.js';
import { storage } from '@libs/plugins/storage.js';

const sessionUtils = {
    saveUserInfo(data) {
        storage.saveSession(constantVar.SESSION_KEY_USERINFO, data);
    },
    findUserInfo() {
        return storage.findSession(constantVar.SESSION_KEY_USERINFO);
    },
    removeUserInfo() {
        storage.removeSession(constantVar.SESSION_KEY_USERINFO);
    },
    saveToken(data) {
        storage.saveSession(constantVar.SESSION_KEY_TOKEN, data);
    },
    findToken() {
        return storage.findSession(constantVar.SESSION_KEY_TOKEN);
    },
    removeToken() {
        storage.removeSession(constantVar.SESSION_KEY_TOKEN);
    },
    /**
     * @param {Object} data 
     */
    saveUserConfig(data) {
        let userConfig = storage.findSession(constantVar.SESSION_KEY_USERCFG);
        userConfig = Object.assign({}, userConfig, data);
        storage.saveSession(constantVar.SESSION_KEY_USERCFG, userConfig);
    },
    findUserConfig() {
        return storage.findSession(constantVar.SESSION_KEY_USERCFG) || {};
    },

    // 
    saveMerchantRangeTree(data){
        storage.saveSession(constantVar.SESSION_MERCHANT_RANGE_TREE, data);
    },
    findMerchantRangeTree(){
        return storage.findSession(constantVar.SESSION_MERCHANT_RANGE_TREE);
    },
};

export default sessionUtils;



