/**
 * @description api
 *
 */

import SACPApis from './requestApis.sacp.js';
import PmsApis from './requestApis.pms.js';
const install = function (Vue, { httpFactory, httpConfig, toast }) {
    if (install.installed) return;

    let http = httpFactory.create(httpConfig);

    Vue.prototype.$pmsApis = new PmsApis(http, toast);
    Vue.prototype.$sacpApis = new SACPApis(http, toast);

    let config2 = Object.assign({}, httpConfig, { 
        baseURL: httpConfig.dicURL,
        interceptor: httpConfig.interceptor,
        router: httpConfig.router,
    });
    let http2 = httpFactory.create(config2);
    // Vue.prototype.$dicApis = new DicApis(http2, toast);
};

export default { install };



