/**
 * @description 
 */

import userAgentUtils from './userAgentUtils.js'
import { dateUtils } from '../../plugins/dateUtils.js'

// 
const RESPONSE_SUCCESS_CODE = 0

/**
 * @description 
 */
class SimpleToast {
    constructor() {}
    success(msg) {
        alert(msg)
    }
    error(msg) {
        alert(msg)
    }
}

/**
 * @description 
 */
class RequestMethod {
    static get GET() {
        return 'get'
    }
    static get POST() {
        return 'post'
    }
    static get POST_JSON() {
        return 'postJson'
    }
    static get PUT() {
        return 'put'
    }
    static get PUT_JSON() {
        return 'putJson'
    }
    static get DELETE() {
        return 'delete'
    }
    static get PATCH() {
        return 'patch'
    }
    static get DOWNLOAD() {
        return 'download'
    }
}

/**
 * @description 
 */
class ActionResult {
    /**
     * @param {Boolean | Function} success 
     * @param {Function} successHandler 
     * @param {Function} failHandler 
     * @param {String} successMsg 
     * @param {String} failMsg 
     */
    constructor(success, successHandler, failHandler, successMsg, failMsg) {
        this.success = success
        this.successHandler = successHandler
        this.failHandler = failHandler
        this.successMsg = successMsg
        this.failMsg = failMsg

        this.applyResponseMessage = false
    }

    async getResult(code, data, msg) {
        switch (typeof this.success) {
            case 'function':
                this.success = await this.success(code, data, msg)
                break
            case 'boolean':
                break
            default:
                this.success = code === RESPONSE_SUCCESS_CODE
                break
        }

        return {
            success: this.success,
            dataHandler: this.success ? this.successHandler : this.failHandler,
            message: this.success ? this.successMsg : this.failMsg,
        }
    }
}

/**
 * @class ResponseResult
 * @description api
 *
 */
class ResponseResult {
    constructor(responseData, responseHandler, toast) {
        this.responseData = responseData
        this.responseHandler = responseHandler
        this.toast = toast || new SimpleToast()
    }

    async extract() {
        let resData = await this.responseData

        let code = resData ? resData.code : null
        let data = resData ? resData.data : null
        let msg = resData ? resData.msg : null

        if (typeof this.responseHandler === 'function') {
            resData = await this.responseHandler(code, data, msg)
        } else if (this.responseHandler instanceof ActionResult) {
            let result = await this.responseHandler.getResult(code, data, msg)
            if (typeof result.dataHandler === 'function') {
                resData = await result.dataHandler(code, data, msg)
            }
            let message =
                result.message ||
                (result.success && !this.responseHandler.applyResponseMessage
                    ? ''
                    : msg)
            setTimeout(() => {
                result.success && message
                    ? this.toast.success(message)
                    : this.toast.error(message)
            }, 50)
        }

        return resData
    }

    // 
    // start
    // ================================================================================================================

    /**
     * @description 
     * @param {Number} code 
     * @param {Any} data 
     * @param {String} msg 
     */
    static commonDataHandler(code, data, msg) {
        let rst = {}
        if (code === RESPONSE_SUCCESS_CODE && data) {
            rst = data
            if (rst.beginTime)
                rst.beginTime = dateUtils.format(
                    new Date(rst.beginTime),
                    'yyyy-MM-dd HH:mm:ss'
                )
            if (rst.realBeginTime)
                rst.realBeginTime = dateUtils.format(
                    new Date(rst.realBeginTime),
                    'yyyy-MM-dd HH:mm:ss'
                )
            if (rst.endTime)
                rst.endTime = dateUtils.format(
                    new Date(rst.endTime),
                    'yyyy-MM-dd HH:mm:ss'
                )
            if (rst.finishedTime)
                rst.finishedTime = dateUtils.format(
                    new Date(rst.finishedTime),
                    'yyyy-MM-dd HH:mm:ss'
                )
            if (rst.createTime)
                rst.createTime = dateUtils.format(
                    new Date(rst.createTime),
                    'yyyy-MM-dd HH:mm:ss'
                )
            if (rst.updateTime)
                rst.updateTime = dateUtils.format(
                    new Date(rst.updateTime),
                    'yyyy-MM-dd HH:mm:ss'
                )
        }
        return rst
    }

    /**
     * @description 
     * @param {Number} code 
     * @param {Any} data 
     * @param {String} msg 
     */
    static commonPageDataHandler(code, data, msg) {
        let rst = { data: [], total: 0 }
        if (
            code === RESPONSE_SUCCESS_CODE &&
            data &&
            data.list instanceof Array
        ) {
            rst = {
                data: data.list.map((item) => {
                    if (item.beginTime)
                        item.beginTime = dateUtils.format(
                            new Date(item.beginTime),
                            'yyyy-MM-dd HH:mm:ss'
                        )
                    if (item.realBeginTime)
                        item.realBeginTime = dateUtils.format(
                            new Date(item.realBeginTime),
                            'yyyy-MM-dd HH:mm:ss'
                        )
                    if (item.endTime)
                        item.endTime = dateUtils.format(
                            new Date(item.endTime),
                            'yyyy-MM-dd HH:mm:ss'
                        )
                    if (item.finishedTime)
                        item.finishedTime = dateUtils.format(
                            new Date(item.finishedTime),
                            'yyyy-MM-dd HH:mm:ss'
                        )
                    if (item.createTime)
                        item.createTime = dateUtils.format(
                            new Date(item.createTime),
                            'yyyy-MM-dd HH:mm:ss'
                        )
                    if (item.updateTime)
                        item.updateTime = dateUtils.format(
                            new Date(item.updateTime),
                            'yyyy-MM-dd HH:mm:ss'
                        )
                    return item
                }),
                total: data.total || 0,
            }
        }
        return rst
    }

    /**
     * @description 
     * @param {Number} code 
     * @param {Any} data 
     * @param {String} msg 
     */
    static commonArrayDataHandler(code, data, msg) {
        let rst = []
        if (code === RESPONSE_SUCCESS_CODE && data instanceof Array) {
            rst = data.map((item) => {
                if (item.beginTime)
                    item.beginTime = dateUtils.format(
                        new Date(item.beginTime),
                        'yyyy-MM-dd HH:mm:ss'
                    )
                if (item.realBeginTime)
                    item.realBeginTime = dateUtils.format(
                        new Date(item.realBeginTime),
                        'yyyy-MM-dd HH:mm:ss'
                    )
                if (item.endTime)
                    item.endTime = dateUtils.format(
                        new Date(item.endTime),
                        'yyyy-MM-dd HH:mm:ss'
                    )
                if (item.finishedTime)
                    item.finishedTime = dateUtils.format(
                        new Date(item.finishedTime),
                        'yyyy-MM-dd HH:mm:ss'
                    )
                if (item.createTime)
                    item.createTime = dateUtils.format(
                        new Date(item.createTime),
                        'yyyy-MM-dd HH:mm:ss'
                    )
                if (item.updateTime)
                    item.updateTime = dateUtils.format(
                        new Date(item.updateTime),
                        'yyyy-MM-dd HH:mm:ss'
                    )
                return item
            })
        }
        return rst
    }

    /**
     * @description 
     * @param {Number} code 
     * @param {Any} data 
     * @param {String} msg 
     */
    static commonActionResultHandler(code, data, msg) {
        let rst = { success: false }
        if (code === RESPONSE_SUCCESS_CODE) rst.success = true
        return rst
    }

    // end
    // ================================================================================================================

    // 
    // start
    // ================================================================================================================

    /**
     * @description keyvalue
     * @param {String} key label
     * @param {String} value value
     */
    static commonOptionsHandler(key, value) {
        return (code, data, msg) => {
            let rst = []
            if (code === RESPONSE_SUCCESS_CODE && data instanceof Array) {
                rst = data.map((item) => ({
                    label: item[key],
                    value: item[value],
                }))
            }
            return rst
        }
    }

    /**
     * @description labelvalue
     * @param {String} key label
     * @param {String} value value
     * @param {Boolean} override lablevaluefalse
     */
    static commonOptionalDataHandler(key, value, override) {
        return (code, data, msg) => {
            let rst = []
            if (code === RESPONSE_SUCCESS_CODE && data instanceof Array) {
                rst = data.map((item) => {
                    item.label =
                        override === true
                            ? item[key]
                            : item.hasOwnProperty('label')
                            ? item.label
                            : item[key]
                    item.value =
                        override === true
                            ? item[value]
                            : item.hasOwnProperty('value')
                            ? item.value
                            : item[value]
                    return item
                })
            }
            return rst
        }
    }

    /**
     * @description 
     * @param {String} successMsg
     */
    static commonActionToastHandler(successMsg) {
        return ResponseResult.actionHandler({
            success: (code, data, msg) =>
                Boolean(code === RESPONSE_SUCCESS_CODE),
            successHandler: (code, data, msg) => ({
                success: Boolean(code === RESPONSE_SUCCESS_CODE),
            }),
            failHandler: null,
            successMsg: successMsg,
            failMsg: null,
        })
    }

    /**
     * @description 
     */
    static commonActionHandler() {
        return ResponseResult.actionHandler({})
    }

    /**
     * @description 
     * @param {Boolean | Function} success 
     * @param {Function} successHandler 
     * @param {Function} failHandler 
     * @param {String} successMsg 
     * @param {String} failMsg 
     */
    static actionHandler({
        success,
        successHandler,
        failHandler,
        successMsg,
        failMsg,
    }) {
        return new ActionResult(
            success,
            successHandler,
            failHandler,
            successMsg,
            failMsg
        )
    }

    // end
    // ================================================================================================================
}

/**
 * @description 
 */
class RequestEntity {
    constructor(http, toast) {
        this.http = http
        this.toast = toast

        this._url = ''
        this._method = 'get'
        this._config = {}
        this._params = {}
        this._responseHandler = ''
    }

    url(url) {
        this._url = url
        return this
    }
    method(method) {
        this._method = method
        return this
    }
    params(params) {
        this._params = params
        return this
    }
    config(config) {
        this._config = config
        return this
    }

    /**
     * @description 
     * @param {Function} responseHandler  function(code, data, msg)
     */
    submit(responseHandler) {
        this._responseHandler = responseHandler

        let requestResult
        let url = this._url,
            params = this._params,
            config = this._config

        switch (this._method) {
            case RequestMethod.GET:
                requestResult = this.http.get(url, params, config)
                break
            case RequestMethod.POST:
                requestResult = this.http.post(url, params, config)
                break
            case RequestMethod.POST_JSON:
                requestResult = this.http.postJson(url, params, config)
                break
            case RequestMethod.PUT:
                requestResult = this.http.put(url, params, config)
                break
            case RequestMethod.PUT_JSON:
                requestResult = this.http.putJson(url, params, config)
                break
            case RequestMethod.DELETE:
                requestResult = this.http.delete(url, params, config)
                break
            case RequestMethod.PATCH:
                requestResult = this.http.patch(url, params, config)
                break
            case RequestMethod.DOWNLOAD:
                requestResult = this.http.download(url, config ? config.filename : '', params, config.isPost)
                break

                
        }

        return new ResponseResult(
            requestResult,
            this._responseHandler,
            this.toast
        )
    }
}

/**
 * @class RequestProxy
 * @description api
 */
class RequestProxy {
    constructor(http, toast) {
        this._http = http
        this._toast = toast
        this.isIE = userAgentUtils.isIE()
    }

    /**
     * @description 
     * @param {String} method 
     * @param {String} url 
     */
    _createRequestEntity(method, url) {
        return new RequestEntity(this._http, this._toast)
            .url(url)
            .method(method)
    }

    /**
     * @description 
     * @param RequestMethod method 
     * @param {String} url 
     */
    http(method, url) {
        return this._createRequestEntity(method, url)
    }

    // 
    get(url) {
        return this._createRequestEntity(RequestMethod.GET, url)
    }
    post(url) {
        return this._createRequestEntity(RequestMethod.POST, url)
    }
    postJson(url) {
        return this._createRequestEntity(RequestMethod.POST_JSON, url)
    }
    put(url) {
        return this._createRequestEntity(RequestMethod.PUT, url)
    }
    putJson(url) {
        return this._createRequestEntity(RequestMethod.PUT_JSON, url)
    }
    delete(url) {
        return this._createRequestEntity(RequestMethod.DELETE, url)
    }
    patch(url) {
        return this._createRequestEntity(RequestMethod.PATCH, url)
    }
    download(url) {
        return this._createRequestEntity(RequestMethod.DOWNLOAD, url)
    }

    /**
     * @description apiPromiseinstance
     * @param {Object} instance 
     * @param {Array<String>} whiteList 
     *
     * instance
     *      Promise
     *      ResponseResultPromise
     *      RequestEntityResponseResultPromise
     *      AnyPromise.resolve
     */
    scanApis(instance, whiteList) {
        // console.log('gggg2', Object.getOwnPropertyNames(instance.__proto__))

        if (!instance.__proto__) return
        let wList = ['constructor']
        if (whiteList instanceof Array) wList = wList.concat(whiteList)

        Reflect.ownKeys(instance.__proto__).forEach((funcName) => {
            if (wList.indexOf(funcName) === -1) {
                instance[funcName] = instance[funcName].bind(instance)

                if (this.isIE) {
                    let funcProto = instance[funcName]
                    instance[funcName] = function () {
                        let result = funcProto.call(instance, ...arguments)
                        if (result instanceof Promise) {
                            return result
                        } else if (result instanceof ResponseResult) {
                            return result.extract()
                        } else if (result instanceof RequestEntity) {
                            return result.submit().extract()
                        } else {
                            return Promise.resolve(result)
                        }
                    }
                } else {
                    instance[funcName] = new Proxy(instance[funcName], {
                        apply(target, thisArg, argumentsList) {
                            let result = target(...argumentsList)
                            if (result instanceof Promise) {
                                return result
                            } else if (result instanceof ResponseResult) {
                                return result.extract()
                            } else if (result instanceof RequestEntity) {
                                return result.submit().extract()
                            } else {
                                return Promise.resolve(result)
                            }
                        },
                    })
                }
            }
        })
    }
}

export { RequestMethod, RequestProxy, ResponseResult }



