/**
 * 
 * ====================================================================================================================
 */

import apis from './apiDefinition.js';
import sessionUtils from './session/sessionUtils.js';
import { strTpl } from '../plugins/stringTemplate.js';
import { constantVar } from '../configs/constantVar.js';
import { RequestProxy, ResponseResult } from '@libs/utils/requestProxy.js';

class PmsApis extends RequestProxy {
    constructor(http, toast) {
        super(http, toast);
        super.scanApis(this);
    }

    // 
    login({ username, password }) {
        return super
            .get(apis.API_PMS_LOGIN)
            .params({
                account: username,
                password: password,
            })
            .submit(
                ResponseResult.actionHandler({
                    success: (code, data, msg) => Boolean(code === 0 && data),
                    successHandler: (code, data, msg) => {
                        sessionUtils.saveToken(data.token);
                        sessionUtils.saveUserInfo({
                            loginId: data.loginId,
                            username: data.name || data.loginId,
                        });
                        return data;
                    },
                    successMsg: strTpl.parseCode(
                        constantVar.CODE.LOGIN_SUCCESS
                    ),
                })
            );
    }

    /**
     * @description 
     */
    logout() {
        sessionUtils.removeToken();
        sessionUtils.removeUserInfo();
        return 0;
    }

    queryMembers({ name }) {
        return super
            .get(apis.API_PMS_QUERY_MEMBER_LIST)
            .params({
                realName: name,
            })
            .submit(ResponseResult.commonArrayDataHandler);
    }

    // 
    // start
    // ==========================================================================================

    // 
    queryProjList({ pageNum, pageSize, filters }) {
        return super
            .get(apis.API_PMS_QUERY_PROJ_LIST)
            .params(
                Object.assign(
                    {},
                    {
                        pageNum: pageNum,
                        pageSize: pageSize,
                    },
                    filters || {}
                )
            )
            .submit(ResponseResult.commonPageDataHandler);
    }

    // 
    createProjectInfo(formData) {
        return super
            .postJson(apis.API_PMS_CREATE_PROJ_INFO)
            .params(formData)
            .submit(
                ResponseResult.commonActionToastHandler(
                    strTpl.parseCode(constantVar.CODE.ACTION_SUCCESS)
                )
            );
    }

    // 
    updateProjectInfo(formData) {
        return super
            .postJson(apis.API_PMS_UPDATE_PROJ_INFO)
            .params(formData)
            .submit(
                ResponseResult.commonActionToastHandler(
                    strTpl.parseCode(constantVar.CODE.ACTION_SUCCESS)
                )
            );
    }
}

export default PmsApis;



