// 
// import 'babel-polyfill';
// import '@libs/plugins/polyfill.js';

import Vue from 'vue';
import App from './App';
import store from './vuex/store.js';
import { VueRouter, router } from './router/router.js';

// 

import VueDraggableResizable from 'vue-draggable-resizable'
Vue.component('vue-draggable-resizable',VueDraggableResizable)

Vue.config.productionTip = false;

/**
 * project configs
 * =====================================================================================
 */
import ApiCfg from './configs/apiconfig.js';
import ConstantVar from './configs/constantVar.js';
import DirectiveCfg from './configs/directiveConfig.js';
import HttpInterceptorCfg from './configs/httpInterceptorCfg.js';

/**
 * base plugins
 * =====================================================================================
 */

import Storage from '@libs/plugins/storage.js';
import Formatter from '@libs/utils/formatter.js';
import ArrayUtils from '@libs/utils/arrayUtils.js';
import numberContrast from '@libs/utils/numberContrast.js';
import Directive from '@libs/directives/_installer.js';
import { httpFactory } from '@libs/plugins/http.js';


import DateUtils from './plugins/dateUtils.js';
import GuidUtils from './plugins/guidUtils.js';
import ValidateUtils from './plugins/validateUtils.js';
import GlobalDirectives from './plugins/globalDirectives.js';
import RequestApis from './data/requestApis.js';
import StringTemplate from './plugins/stringTemplate.js';

/**
 * ui plugins
 * =====================================================================================
 */
import './assets/css/reset.scss';
import './assets/css/index.scss';
import Antd from 'ant-design-vue';
import 'ant-design-vue/dist/antd.css';
import './plugins/vxe-table';

import 'font-awesome/css/font-awesome.min.css';


/**
 * load plugins
 * =====================================================================================
 */

// 
Vue.use(VueRouter);
Vue.use(Storage);
Vue.use(Formatter);
Vue.use(ArrayUtils);
Vue.use(numberContrast);
Vue.use(Directive, DirectiveCfg);
Vue.use(Antd);


// 
Vue.use(RequestApis, {
    httpFactory: httpFactory,
    httpConfig: {
        baseURL: ApiCfg.baseUrl,
        esearchURL: ApiCfg.esearchUrl,
        dicURL: ApiCfg.dicUrl,
        interceptor: HttpInterceptorCfg,
        router: router,
    },
    toast: {
        success(msg) {
            if (window.appContext.$message)
                window.appContext.$message({ type: 'success', message: msg });
        },
        error(msg) {
            if (window.appContext.$message)
                window.appContext.$message({ type: 'error', message: msg });
        },
    },
});
Vue.use(DateUtils);
Vue.use(GuidUtils);
Vue.use(ValidateUtils);
Vue.use(GlobalDirectives);
Vue.use(ConstantVar);
Vue.use(StringTemplate);


window.appContext = new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>',
});



