/**
 * @module encryptUtils
 * @description [2019-11-26] - js
 * @author KKPE
 * @version 1.0.0
 * 
 */


import CryptoJS from 'crypto-js';

class EncryptUtils {
    
    constructor() {
        this.defaultIv = '1234567812345678'; // DES/AES
    }


    // md5 
    // ============================================================================

    /**
     * @description md5
     * @param {String} data 
     */
    generateMD5(data) {
        return CryptoJS.MD5(data).toString();
    }

    /**
     * @description md5
     * @param {String} data 
     */
    generateMd5Upper(data) {
        return this.generateMD5(data).toUpperCase();
    }

  
    // Base64 /
    // ============================================================================

    /**
     * @description base64
     * @param {String} data 
     */
    encodeBase64(data) {
        return CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(data));
    }

    /**
     * @description base64
     * @param {String} data 
     */
    decodeBase64(data) {
        return CryptoJS.enc.Base64.parse(data).toString(CryptoJS.enc.Utf8);
    }


    // DES /
    // ============================================================================


    /**
     * @description DES
     * 
     * @param {String} data 
     * @param {String} key 
     * @param {String} iv 
     * 
     * @returns {String} Base64
     */
    encryptDES(data, key, iv) {
        return CryptoJS.DES.encrypt(data, CryptoJS.enc.Utf8.parse(key), {        
            iv: CryptoJS.enc.Utf8.parse(iv || this.defaultIv),
            mode:CryptoJS.mode.CBC,
            padding:CryptoJS.pad.Pkcs7
        }).toString();
    }

    /**
      * @description DES
      * 
      * @param {String} data 
      * @param {String} key 
      * @param {String} iv 
      */
    decryptDES(ciphertext, key, iv) {
        let bytes  = CryptoJS.DES.decrypt(ciphertext, CryptoJS.enc.Utf8.parse(key), {
            iv: CryptoJS.enc.Utf8.parse(iv || this.defaultIv),
            mode: CryptoJS.mode.CBC,
            padding: CryptoJS.pad.Pkcs7
        });
        return bytes.toString(CryptoJS.enc.Utf8);
    }


    // AES /
    // ============================================================================


    /**
     * @description AES
     * 
     * @param {String} data 
     * @param {String} key 
     * @param {String} iv 
     * 
     * @returns {String} Base64
     */
    encryptAES(data, key, iv) {
        return CryptoJS.AES.encrypt(data, CryptoJS.enc.Utf8.parse(key), {        
            iv: CryptoJS.enc.Utf8.parse(iv || this.defaultIv),
            mode:CryptoJS.mode.CBC,
            padding:CryptoJS.pad.Pkcs7
        }).toString();
    }

    /**
      * @description AES
      * 
      * @param {String} data 
      * @param {String} key 
      * @param {String} iv 
      */
    decryptAES(ciphertext, key, iv) {
        let bytes  = CryptoJS.AES.decrypt(ciphertext, CryptoJS.enc.Utf8.parse(key), {
            iv: CryptoJS.enc.Utf8.parse(iv || this.defaultIv),
            mode: CryptoJS.mode.CBC,
            padding: CryptoJS.pad.Pkcs7
        });
        return bytes.toString(CryptoJS.enc.Utf8);
    }





}
 



const install = function(Vue) {
    Vue.prototype.$encryptUtils = new EncryptUtils();
}
export default { install };

const encryptUtils = new EncryptUtils();
export { encryptUtils };


