import Vue from 'vue';

const comBlank = Vue.extend({
  template: '<router-view></router-view>'
});

const comIndex = () => import( /* webpackChunkName: 'comIndex' */ '../pages/index.vue');


const comError = () => import( /* webpackChunkName: 'comError' */ '../pages/errors/error.vue');
const comError401 = () => import( /* webpackChunkName: 'comError401' */ '../pages/errors/401.vue');
const comError404 = () => import( /* webpackChunkName: 'comError404' */ '../pages/errors/404.vue');

const merchantRangeSelection = () => import('../pages/merchantRangeSelection.vue');
const matchBusinessData = () => import('../pages/matchBusinessData.vue');
const merchantList = () => import('../pages/merchantList.vue');
const relatedPartyMatch = () => import('../pages/relatedPartyMatch.vue');
const associatedTransactionVoucherTable = () => import('../pages/associatedTransactionVoucherTable.vue');
const index_1 = () => import('../pages/index_1.vue');
const index_2 = () => import('../pages/index_2.vue');
const mergeCustomerTable = () => import('../pages/mergeCustomerTable.vue');


const staticRoute = [{
    path: '/',
    redirect: '/index'
  },
  {
    path: '/index',
    component: comIndex,
    name: 'index',
    children: [{
        path: '/merchantRangeSelection',
        name: 'merchantRangeSelection',
        component: merchantRangeSelection
      },
      {
        path: '/matchBusinessData',
        name: 'matchBusinessData',
        component: matchBusinessData
      },
      {
        path: '/merchantList',
        name: 'merchantList',
        component: merchantList
      },
      {
        path: '/relatedPartyMatch',
        name: 'relatedPartyMatch',
        component: relatedPartyMatch
      },
    ]
  }, {
    path: '*',
    redirect: '/error/404'
  },
  {
    path: '/error',
    component: comError,
    children: [{
        path: '401',
        component: comError401
      },
      {
        path: '404',
        component: comError404
      }
    ]
  }, {
    path: '/associatedTransactionVoucherTable',
    component: associatedTransactionVoucherTable,
    name: 'associatedTransactionVoucherTable',
  },
  {
    path: '/mergeCustomerTable',
    name: 'mergeCustomerTable',
    component: mergeCustomerTable
  },
  {
    path: '/index_1',
    component: index_1,
    name: 'index_1',
  },
  {
    path: '/index_2',
    component: index_2,
    name: 'index_2',
  }
]

export default staticRoute;



