const parseDate = function(date) {
    if(typeof date === 'string') {
        if(date.length < 19) return '';

        let d = date.substr(0, 19);
        if(!d) return '';
        let arr = []; 
        
        if(date.indexOf('T') > -1) {
            arr = d.split('T');
        } else {
            arr[0] = date.substr(0, 10);
            arr[1] = date.substr(12, 8);
        }
        
        if(arr.length != 2) return '';
        let s_date, s_time, arr_date, arr_time;

        if(arr.length >= 1) s_date = arr[0];
        if(arr.length >= 2) s_time = arr[1];

        if(s_date) arr_date = s_date.split('-');
        if(s_time) arr_time = s_time.split(':');

        if(arr_date.length > 0) {
            return new Date(
                parseInt(arr_date[0]),
                (parseInt(arr_date[1]) - 1) || 0,
                parseInt(arr_date[2]) || 0,
                parseInt(arr_time[0]) || 0,
                parseInt(arr_time[1]) || 0,
                parseInt(arr_time[2]) || 0
            );
        }
    }

    return null;
} 

const dateFormatter = function(date, format) {
    if(!date) return '';

    if(!(date instanceof Date)) {
        date = parseDate(date);
    }
    
    if(!date) return '';

    format = format || 'yyyy-MM-dd hh:mm:ss';

    let o = {
        "M+" : date.getMonth()+1, //
        "d+" : date.getDate(), //
        "h+" : date.getHours()%12 == 0 ? 12 : date.getHours()%12, //
        "H+" : date.getHours(), //
        "m+" : date.getMinutes(), //
        "s+" : date.getSeconds(), //
        "q+" : Math.floor((date.getMonth()+3)/3), //
        "S" : date.getMilliseconds() //
    };

    let week = {
        "0" : "/u65e5",
        "1" : "/u4e00",
        "2" : "/u4e8c",
        "3" : "/u4e09",
        "4" : "/u56db",
        "5" : "/u4e94",
        "6" : "/u516d"
    };

    if(/(y+)/.test(format)){
        format = format.replace(RegExp.$1, (date.getFullYear()+"").substr(4 - RegExp.$1.length));
    }

    if(/(E+)/.test(format)){
        format = format.replace(RegExp.$1, ((RegExp.$1.length>1) ? (RegExp.$1.length>2 ? "/u661f/u671f" : "/u5468") : "")+week[this.getDay()+""]);
    }

    for(var k in o){
        if(new RegExp("("+ k +")").test(format)){
            format = format.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
        }
    }

    return format;
};

const dateUtils = {
    format(date, format) {
        if(!date) return '';
        
        return dateFormatter(date, format);
        // return '' + date.getFullYear() + (date.getMonth() + 1) + date.getDate();
    },
    parseDate(dateStr) {
        return parseDate(dateStr);
    },
    differToday(date, unit) {
        if(!date || !(date instanceof Date)) return null;

        unit = unit || 'd';
        let now = new Date();
        let differMs = parseInt(date - now);

        switch(unit) {
            case 'y': return differMs / 1000 / 60 / 60 / 24 / 365;
            case 'M': return differMs / 1000 / 60 / 60 / 24 / 30;
            case 'd': return differMs / 1000 / 60 / 60 / 24;
            case 'h': return differMs / 1000 / 60 / 60;
            case 'm': return differMs / 1000 / 60;
            case 's': return differMs / 1000;
        }
    },
    formatDifferDays(leftDays) {
        if(!leftDays) return '';

        let decimal = leftDays - parseInt(leftDays);
        let s = parseInt(leftDays) + '' + (decimal > 0.04 ? parseInt(decimal * 24) + '' : '');
        
        if (leftDays < 1) {
            leftDays = leftDays * 24;
            decimal = leftDays - parseInt(leftDays);
            s = parseInt(leftDays) + '' + (decimal > 0.04 ? parseInt(decimal * 60) + '' : '');
        }

        if (leftDays < 1) {
            leftDays = leftDays * 60;
            s = parseInt(leftDays) + '';
        }

        return s;
    },
    isToday(date) {
        let now = new Date();

        let nowStr = this.format(now);
        return !!date && nowStr === date;
    },
    getToday() {
        let now = new Date();
        return this.format(now);
    },
    getFullToday() {
        return dateFormatter(new Date(), 'yyyy-MM-dd hh:mm:ss');
    },
    dateAdd(date, num) {
        let now = new Date(date);
        now.setDate(now.getDate() + num);
        return now;
    },
    getSevenDays() {
        let res = [];
        let now = new Date();
        for (let i = -3; i <= 3; i++) {
            let day = this.dateAdd(now, i);
            res.push({'date': day.getMonth() + 1 + '.' + day.getDate()});
        }
        return res;
    }
};

const install = function (Vue) {
    Vue.prototype.$dateUtils = dateUtils;
};

export default { install };
export { dateUtils };



