import sessionUtils from '../data/session/sessionUtils.js';

import CN from '../lang/CN.js';
import TC from '../lang/TC.js';
import EN from '../lang/EN.js';

const DEFAULT_LANG = 'CN';
const LANG_LIB = { CN, TC, EN };

const stringTemplateParser = {
    lang: '',
    langLib: '',
    init() {
        this.checkLang();
    },
    checkLang() {
        if (!this.lang) {
            this.lang = sessionUtils.findUserConfig.lang || DEFAULT_LANG;
            this.langLib = LANG_LIB[this.lang];
        }
    },
    codeParse(strings, code) {
        return strings[0] + (this.langLib[code] || code) + strings[1];
    },
    langParse(strings, lang) {},
};

const strTpl = {
    parseCode(code) {
        return stringTemplateParser.codeParse`${code}`;
    },
    text(s) {},
};

const install = function (Vue) {
    for (let key in stringTemplateParser) {
        if (typeof stringTemplateParser[key] === 'function') {
            stringTemplateParser[key] = stringTemplateParser[key].bind(
                stringTemplateParser
            );
        }
    }
    stringTemplateParser.init();

    Vue.prototype.$strTpl = strTpl;
};

export default { install };
export { strTpl };



