export default {
    setMenuId(content){
        console.log(content,'content')
        content.commit('setMenuId');
    },
    zoomState(content,payload){
        content.commit('keepZoomState',payload);
    },
    tableColumn(content,payload){
        content.commit('addTable',payload);
    },
    hanlderCustomTransactionBut(content,payload){
        content.commit('customTransactionButState',payload);
    },
    handlerUserInfo(content,payload){
        content.commit('saveUserInfo',payload);
    },
    handlerPublicParam(content,payload){
        content.commit('savePublicParam',payload);
    }
};


