const constantVar = {
    SESSION_KEY_USERINFO: 'userInfo',
    SESSION_KEY_TOKEN: 'token',
    SESSION_KEY_USERCFG: 'userConfig',

    /**
     * Database
     * =========================================================================
     */

    /**
     * code
     *
     * [0-2]  000: 100: ; 101: ;
     * [3]    1: ; 2: 
     * [4-6]  000: 001: ; 002: 
     * [7-9]  
     */
    CODE: {
        ACTION_SUCCESS: '0001000001',
        ACTION_FAIL: '0002000002',

        LOGIN_SUCCESS: '1001001001',
        LOGIN_ERROR: '1002001002',
    },
    /**
     * 
     */
    SESSION_MERCHANT_RANGE_TREE:'treeArray', //
};

const install = function (Vue, config) {
    Vue.prototype.$constantVar = constantVar;
};

export default { install };
export { constantVar };



