import axios from 'axios';
import { message } from 'ant-design-vue';
import sessionUtils from '@data/session/sessionUtils.js';

/**
 * Http
 * @class HttpHelper
 */
class HttpHelper {

    constructor() {
        this.requestList = [];
    }

    /**
     * 
     *
     * @param {String} url url
     * @param {Function} cancelCallback ajax
     *
     */
    checkRepeatRequest(url, cancelCallback) {
        if (!url) return;

        for (let u of this.requestList) {
            if (u == url) {
                cancelCallback();
                return;
            }
        }

        this.requestList.push(url);
    }

    /**
     * 
     *
     * @param {String} url url
     * @todo 
     *
     */
    clearFinishedRequest(url) {
        this.requestList.splice(this.requestList.findIndex(item => item == url), 1);
    }

    /**
     * @description get
     * @param {String} url 
     * @param {Object} paramObj 
     */
    static stringifyUrlParams(url, paramObj) {
        if (!url || !paramObj) return url;
        return url + (url.indexOf('?') > -1 ? '&' : '?') + HttpHelper.jointParams(paramObj);
    }

    /**
     * @description 
     * @param {Object} paramObj 
     */
    static jointParams(paramObj) {
        if (!paramObj) return '';
        let keyVal = [];
        for (let key in paramObj) {
            if (paramObj[key] instanceof Array) {
                keyVal = keyVal.concat(paramObj[key].map(val => (key + '=' + encodeURIComponent(val))));

            } else if (paramObj[key] == null) {
                keyVal.push(key + '=');

            } else {
                keyVal.push(key + '=' + encodeURIComponent(paramObj[key]));
            }
        }
        return keyVal ? keyVal.join('&') : '';
    }

    /**
     * @description url
     * @param {String} url 
     * @param {String} key 
     * 
     */
    static queryValFromUrl(url, key) {
        if (!url || !key) return '';

        let arr = url.split('?');
        let params = (url.indexOf('?') > -1 && arr && arr.length == 2) ? arr[1] : url;
        let reg = new RegExp("(^|&)" + key + "=([^&]*)(&|$)", "i");
        let r = params.match(reg);
        if (r) return decodeURI(r[2]);
        return '';
    }

    /**
     * @description blob
     */
    static parseBlobToString(blob) {
        if (!blob) return Promise.reject(new Error('nodata'));

        return new Promise((reslove, reject) => {
            let reader = new FileReader();
            reader.readAsText(blob);
            reader.onload = function (event) { reslove(reader.result); };
            reader.onabort = function (event) { reject(new Error('onabort')); };
            reader.onerror = function (event) { reject(new Error('onerror')); };
        });
    }

    /**
     * @description blobbase64
     */
    static parseBlobToDataURL(blob) {
        if (!blob) return Promise.reject(new Error('nodata'));

        return new Promise((reslove, reject) => {
            let reader = new FileReader();
            reader.readAsDataURL(blob);
            reader.onload = function (event) { reslove(reader.result); };
            reader.onabort = function (event) { reject(new Error('onabort')); };
            reader.onerror = function (event) { reject(new Error('onerror')); };
        });
    }

}


/**
 * axios 
 * @class Http
 */
class Http {

    constructor(config) {
        this.JsonContentType = { 'Content-Type': 'application/json;charset=UTF-8' }; // JSONContentType
        this.specificResponseType = ['blob', 'arraybuffer']; // ,,Promise,data
        this.httpHelper = new HttpHelper();
        this.http = axios.create({
            //  'PUT', 'POST'  'PATCH' 
            //  ArrayBuffer Stream
            transformRequest: [(data, headers) => {
                if (!data) return data;
                if (headers['Content-Type']) {
                    if (headers['Content-Type'].indexOf('application/json') > -1) return JSON.stringify(data);
                    if (headers['Content-Type'].indexOf('multipart/form-data') > -1) return data;
                }
                if (!data) data = {}
                let customerInfoFromIndex = sessionUtils.findUserInfo();
                if(!data.projectId||!data.customerId){
                    if (customerInfoFromIndex) {
                        data.customerId = customerInfoFromIndex.curCustomerId;
                        data.projectId = customerInfoFromIndex.curProjectId;
                        data.menuId = localStorage.getItem('menuId') && localStorage.getItem('menuId').replaceAll(`"`, '')
                    }
                }
                // console.log('param',data);
                return HttpHelper.jointParams(data);
            }],
            //  then/catch 
            // transformResponse: [data => data]
        });


        this.defaultConfig = {
            baseURL: '',  // 
            timeout: 500000,  // (0 )
            withCredentials: false, // 
            contentType: 'application/x-www-form-urlencoded;charset=UTF-8', // content-type

            onUploadProgress: function (progressEvent) { // 
                // 
            },
            onDownloadProgress: function (progressEvent) { // 
                // 
            },
            interceptor: null,  // { beforeRequest, afterResponse }
            router: null // vue
        };

        this.__config(Object.assign(this.defaultConfig, config));
    }

    __config(config) {
        if (!config) return;
        this.http.defaults.baseURL = config.baseURL;
        this.http.defaults.timeout = config.timeout;
        this.http.defaults.withCredentials = config.withCredentials;
        this.http.defaults.headers.post['Content-Type'] = config.contentType;
        this.__setRequestInterceptors(config.interceptor, config.router);
        this.__setResponseInterceptors(config.interceptor, config.router);
    }

    /**
     * 
     */
    __setRequestInterceptors(interceptor, router) {
        this.http.interceptors.request.use(
            config => {
                // post
                if (config.method == 'post') {
                    let cancel;
                    config.cancelToken = new axios.CancelToken((c) => { cancel = c; });
                    // this.httpHelper.checkRepeatRequest(config.url + JSON.stringify(config.data || {}), cancel);
                }

                // 
                if (typeof (interceptor.beforeRequest) === 'function') {
                    let interceptResult = interceptor.beforeRequest({
                        success: true,
                        url: config.url,
                        params: config.data,
                        config: config,
                        router: router
                    });
                    if (interceptResult) return interceptResult;
                }
                return config;
            },
            error => {
                // 
                if (typeof (interceptor.beforeRequest) === 'function') {
                    let interceptResult = interceptor.beforeRequest({
                        success: false,
                        url: error.request.config.url,
                        params: error.request.config.data,
                        config: error.request.config,
                        router: router
                    });
                    if (interceptResult instanceof Promise) return interceptResult;
                }
                return Promise.reject(error);
            }
        );
    }

    /**
     * 
     */
    __setResponseInterceptors(interceptor, router) {
        this.http.interceptors.response.use(
            response => {
                // 
                this.httpHelper.clearFinishedRequest(response.config.url + JSON.stringify(response.config.data || {}));

                // 
                if (typeof (interceptor.afterResponse) === 'function') {
                    let interceptResult = interceptor.afterResponse({
                        success: true,
                        url: response.config.url,
                        params: response.config.data,
                        response: response,
                        responseType: response.config.responseType,
                        statusCode: response.status,
                        router: router
                    });
                    if (interceptResult instanceof Promise) return interceptResult;
                }

                let returnFullResponse = (this.specificResponseType && this.specificResponseType.indexOf(response.request.responseType) > -1);
                return Promise.resolve(returnFullResponse ? response : response.data);
            },
            error => {
                if (axios.isCancel(error)) {
                    // return Promise.reject("Ajax Abort: ");

                } else if (error.response) {
                    // 
                    this.httpHelper.clearFinishedRequest(error.response.config.url + JSON.stringify(error.response.config.data || {}));

                    // 
                    if (typeof (interceptor.afterResponse) === 'function') {
                        let callbackResult = interceptor.afterResponse({
                            success: false,
                            url: error.response.config.url,
                            params: error.response.config.data,
                            response: error.response,
                            responseType: error.response.config.responseType,
                            statusCode: error.response.status,
                            router: router
                        });
                        if (callbackResult instanceof Promise) return callbackResult;
                    }
                    return Promise.reject(error.response.data);

                } else {
                    return Promise.reject(error);
                }
            }
        );
    }

    __request(method, url, params, config) {
        if (!params) params = {}
        let customerInfoFromIndex = sessionUtils.findUserInfo()
        if (customerInfoFromIndex) {
            params.customerId = customerInfoFromIndex.curCustomerId;
            params.projectId = customerInfoFromIndex.curProjectId;
            params.menuId = localStorage.getItem('menuId')
        }
        switch (method) {
            case 'get': return this.http.get(url, Object.assign({}, config, { params: params || {} }));
            case 'delete': return this.http.delete(url, Object.assign({}, config, { params: params || {} }));
            case 'put': return this.http.put(url, params, config);
            case 'post': return this.http.post(url, params, config);
            case 'patch': return this.http.patch(url, params, config);
            default: return Promise.reject(new Error('unknown request method.'));
        }
    }

    __requestBlob(method, url, params) {
        return this.http({
            method: method,
            url: method === 'post' ? url : HttpHelper.stringifyUrlParams(url, params),
            data: method === 'post' ? params : null,
            responseType: 'blob'  // blobarraybuffer
        });
    }

    get(url, params, config) { return this.__request('get', url, params, config); }
    delete(url, params, config) { return this.__request('delete', url, params, config); }
    post(url, params, config) { return this.__request('post', url, params, config); }
    postJson(url, params, config) { return this.__request('post', url, params, Object.assign({}, config, { headers: this.JsonContentType })); }
    put(url, params, config) { return this.__request('put', url, params, config); }
    putJson(url, params, config) { return this.__request('put', url, params, Object.assign({}, config, { headers: this.JsonContentType })); }
    patch(url, params, config) { return this.__request('patch', url, params, config); }

    download(url, filename, params, isPost) {
        return new Promise((resolve, reject) => {
            this.__requestBlob(isPost === true ? 'post' : 'get', url, params).then(response => {
                if (!response) return;

                let filenameDef = new Date().getTime();
                let contentDisposition = response.headers['content-disposition'];
                if (contentDisposition) {
                    let arr = contentDisposition.split(';');
                    let filenameinfo = arr.find(item => item.indexOf('filename=') > -1);
                    if (filenameinfo) {
                        let keyVals = filenameinfo.split('=');
                        filenameDef = keyVals && keyVals.length === 2 ? (keyVals[1] || '') : filenameDef;
                        filenameDef = filenameDef.replace('UTF-8\'\'', '');
                    }
                }
                // debugger
                let flag = true
                if(response.headers['content-type']=== 'application/json;charset=UTF-8') {
                    flag = false
                    const reader = new FileReader();
                    reader.readAsText(response.data, 'utf-8');
                    reader.onload = () => {
                        return message.error(JSON.parse(reader.result).resultInfo.statusText)
                    };
                }
                if(!flag) {
                    return
                }
                let link = document.createElement('a');
                link.style.display = 'none';
                link.href = window.URL.createObjectURL(new Blob([response.data]));
                link.download = filename || decodeURIComponent(filenameDef);
                document.body.appendChild(link);
                link.click();
                link.remove();
                resolve({ success: true });
            });

        }).catch(err => { reject(err); });
    }

    preview(url, params, isPost) {
        return new Promise((resolve, reject) => {
            this.__requestBlob(isPost === true ? 'post' : 'get', url, params).then(response => {
                if (!response) {
                    reject(new Error('no response'));

                } else {
                    HttpHelper.parseBlobToDataURL(response.data).then(data => {
                        resolve(data);

                    }).catch(err => { reject(err); });
                }

            }).catch(err => { reject(err); });
        });
    }
}

const install = function (Vue, config) {
    Vue.prototype.$http = new Http(config);
};
export default { install };


const http = new Http();
const httpFactory = { create(config) { return new Http(config); } };
export { http, httpFactory, HttpHelper };



