import XEUtils from 'xe-utils';
import { arrayEach } from 'xe-utils/methods';
const converter = {
    /**
     * 
     *
     * @param {String}          formatType  
     * @param {String | Number} value       
     */
    format(value, formatType){
        if(!formatType) return value;

        switch(formatType) {
            case 'money':
                return this._formatNumber(value, '', true, true, 2);

            case 'count':
                return this._formatNumber(value, '', true, false, 0);

            case 'decimals':
                return this._formatNumber(value, '', false, false, 2);

            default:
                return value;
        }
    },

    /**
     * 
     *
     * @param {Number | String} val           ()
     * @param {String}          symbol        
     * @param {Boolean}         intDivision   
     * @param {Boolean}         decDivision   
     * @param {Number}          decDigits     ()
     */
    _formatNumber(val, symbol, intDivision, decDivision, decDigits) {
        if(typeof val === 'undefined' || val == null || val === '') return val;

        let number = Number(val);
        if(isNaN(val)) return val;

        // 
        if(decDigits && !isNaN(decDigits) && decDigits >= 0) {
            number = Math.round(number * Math.pow(10, decDigits)) / Math.pow(10, decDigits);
        }

        // 
        let valStr = number.toString();
        let valArr = valStr.split('.');

        // 
        let intStr = valArr[0];
        let decStr = valArr.length == 2 ? valArr[1] :
            (decDigits >= 0 ? '0'.repeat(decDigits) : '');

        let j = 1;
        let tmp = '';

        if(intDivision) {
            j = 1;
            tmp = '';
            // 
            for(let i = intStr.length - 1; i >= 0; i--){
                tmp += intStr.charAt(i);
                tmp += (i != 0 && j % 3 == 0 ? ',' : '');
                j++;
            }
            // 
            intStr = tmp.split('').reverse().join('');
        }

        if(decStr !== '') {
            // 
            if(decStr.length < decDigits) {
                decStr = decStr + '0'.repeat(decDigits - decStr.length);
            }

            if(decDivision) {
                j = 1;
                tmp = '';
                // 
                for(let i = 0; i < decStr.length; i++){
                    tmp += decStr.charAt(i);
                    tmp += (i != (decStr.length - 1) && j % 3 == 0 ? ',' : '');
                    j++;
                }
                decStr = tmp;
            }
            decStr = '.' + decStr;
        }

        return (symbol ? symbol : '') + intStr + decStr;
    },
    formatMoney(num) {
        if (isNaN(num)) return num;
        let groups = (/([\-\+]?)(\d*)(\.\d+)?/g).exec('' + num), 
        mask = groups[1],            // 
        integers = (groups[2] || '').split(''), // 
        decimal = groups[3] || '',       // 
        remain = integers.length % 3; 
        
        let temp = integers.reduce(function(previousValue, currentValue, index) { 
            if (index + 1 === remain || (index + 1 - remain) % 3 === 0) { 
                return previousValue + currentValue + ','; 
            } else { 
                return previousValue + currentValue; 
            } 
        }, '').replace(/\,$/g, ''); 
        return mask + temp + decimal; 
    },
    commafy(value, row, index){
        const obj = {
                children: value,
                attrs: {},
            };
        return XEUtils.commafy(value, { digits: 2 })
    },
    fmtEquity(value, row, index) {
        return XEUtils.commafy(value, { digits: 2 }) + '%';
    },
    fmtFinancia(row,cellValue){
        switch (cellValue) {
            case true:
                return '';
            default:
                return '';
        }
    },
    commafyFormat( cellValue, row, index ){
        if (isNaN(cellValue)) return cellValue;

        let groups = (/([\-\+]?)(\d*)(\.\d+)?/g).exec('' + cellValue), 
        mask = groups[1],            //
        integers = (groups[2] || '0').split(''), //
        decimal = groups[3] || '.00',       //
        remain = integers.length % 3; 

        let temp = integers.reduce(function(previousValue, currentValue, index) {
            if (index + 1 === remain || (index + 1 - remain) % 3 === 0) {
                return previousValue + currentValue + ',';
            } else {
                return previousValue + currentValue;
            }
        }, '').replace(/\,$/g, '');
        let total=mask + temp + decimal;
        return total
    },
    mergeCells(dataSource,type){
        const objResult={};
        const arr=[];  //3
        const arr1=[]; //
        const arr2=[0]; //
        const arrResult=[];
        let len=0;
        let typeResult;
        dataSource.map((val)=>{
            for(const i in val){
                if(i === type){
                    typeResult=val[i];
                }
            }
            const objArray=objResult[typeResult] || [];
            objArray.push(val);
            objResult[typeResult]=objArray;
        });
        for(const i in objResult){
            arr.push(objResult[i]);
        }
        arr.map((val,index)=>{
            arr1.push(val.length);
            len += val.length;
            arr2.push(len);
        });
        arrResult[0]=arr1;
        arrResult[1]=arr2;
        return arrResult;
    }
};

const install = function (Vue) {
    Vue.prototype.$formatter = converter;
};

export default { install };

const formatter = converter;
export { formatter };



