/**
 * @module numberContrast
 * @description [2021-2-5] -    1n 
 * @author KKPE
 * @version 1.0.0
 * 
 */

const numberContrast = {
  /**
   * 
   * 
   * src = [1, 2, 3, 4, ..., n]  //  n 
   * 
   * g = f(x1, x2, ..., xm)  m 
   * 
   * f(1)   f(1, 2)   f(1, 2, 3)  f(1, 2, 3, 4) ... f(1, 2, 3, 4, ..., n)
   *        f(1, 3)   f(1, 3, 4)  f(1, 3, 4, 5) ... f(1, 3, 4, 5, ..., n) 
   *        ...
   *        f(1, n-1) f(1, n)
   *        f(1, n)
   * 
   * f(2)   f(2, 3)   f(2, 3, 4)  f(2, 3, 4, 5) ... f(2, 3, 4, 5, ..., n)
   *        f(2, 4)   f(2, 4, 5)  f(2, 4, 5, 6) ... f(2, 4, 5, 6, ..., n) 
   *        ...
   *        f(2, n-1) f(2, n)
   *        f(2, n)
   * 
   * f(3)   ...
   *        ...
   * 
   * f(n-1) f(n-1, n)
   * 
   * f(n)
   * 
   */
  Contrast(standard, mapValues) {
    let sum = (...values) => Number(values.reduce((total, curr) => (total + curr), 0).toFixed(5));
    let leftVals = (start, max) => {
      let vals = [];
      for (let i = start; i <= max; i++) {
        vals.push(i);
      }
      return vals;
    };
    let range = mapValues.length;
    let result = [],
      idxArgs = [],
      valArgs = [];
    for (let i = 0; i < range; i++) {
      // lauout
      if (mapValues[i] === standard) {
        // f(i)
        result.push({
          key: i.toString(),
          value: mapValues[i]
        });
      }
      for (let j = i + 1; j < range; j++) {
        // deep
        for (let k = j; k < range; k++) {
          // length
          idxArgs = leftVals(j, k);
          valArgs = idxArgs.map((item) => mapValues[item]);
          if (sum(mapValues[i], ...valArgs) === standard) {
            result.push({
              key: [i, ...idxArgs].join(','),
              value: [mapValues[i], ...valArgs],
            });
          }
        }
      }
    }
    return result;
  },
}

const numberContrastFun = function (Vue) {
  Vue.prototype.$numberContrast = numberContrast;
};

export default {
  numberContrastFun
};
export {
  numberContrast
};



